import React from 'react';
import PfItem from './PfItem';
import styled from 'styled-components';

const ListBox = styled.div`
margin-top: 30px;
display: flex;
flex-direction: column;
`;
const ListTitle = styled.p`
font-weight: bold;
margin: 0;
}
`;
const List = styled.ul`
list-style: none;
display: flex;
flex-direction: column; 
justify-content: center;
 padding: 0;
`;

const PfList = ({title,pfList,setPfList,checkedList}) => {
    
    return(
        <ListBox>
            <ListTitle>{title}</ListTitle>
            <List>
               {pfList && //pfListê°€ ìˆì„ë•Œë§Œ í•¨
                pfList.map((item)=>
                { 
                    //ë°›ì•„ì˜¨ checkListê°’ì— ë”°ë¼ ë‚´ìš© ì¶œë ¥
                    if(checkedList !== item.done) return null;

                    //ì‚­ì œëœ í¬íŠ¸í´ë¦¬ì˜¤ëŠ” ì¶œë ¥í•˜ì§€ ì•ŠìŒ
                    if(item.deleted) return null;

                    return (
                        <PfItem 
                            key={item.id}
                            pfItem={item}
                            pfList={pfList}
                            setPfList={setPfList}
                            checked={checkedList}
                        />
                    )
                }
                )}
            </List>
        </ListBox>
    );
}

export default PfList;